
<template>
  <form @submit.prevent="handleInput">
    <md-card>
      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field class="md-layout-item md-small-size-100 md-size-70" :class="getValidationClass('info')">
              <label>Thng tin ngi gi</label>
              <md-input type="text" v-model="input.info"></md-input>
              <span class="md-error" v-if="!$v.input.info.required">Thng tin ngi gi khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-33" >
            <md-field :class="getValidationClass('accountNumber')">
              <label>S ti khon</label>
              <md-input type="text" v-model="input.accountNumber"></md-input>
              <span class="md-error" v-if="!$v.input.accountNumber.required">S ti khon khng th b trng</span>
              <span class="md-error" v-else-if="!$v.input.accountNumber.accountNum">S ti khon khng hp l</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field :class="getValidationClass('bankName')">
              <label>Tn chi nhnh</label>
              <md-input type="text" v-model="input.bankName"></md-input>
              <span class="md-error" v-if="!$v.input.bankName.required">Tn ngn hng khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-60">
            <md-field :class="getValidationClass('money')">
                <label>S tin</label>
                <md-input type="text" v-model="input.money"></md-input>
                <span class="md-error" v-if="!$v.input.money.required">S tin khng th b trng</span>
                <span class="md-error" v-else-if="!$v.input.money.valid">S tin khng hp l</span>
            </md-field>
          </div>
           <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field :class="getValidationClass('note')">
                <label>Ni dung</label>
                <md-input type="text" v-model="input.note"></md-input>
                <span class="md-error" v-if="!$v.input.note.required">Ni dung khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-size-100 text-right">
            <md-button class="md-raised md-success" type="submit" >Chuyn tin</md-button>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </form>
</template>
<script>
const moment=require('moment');
import { validationMixin } from 'vuelidate'
import {
  required,
  minValue,
} from 'vuelidate/lib/validators';
const accountNum = (value, vm) => {
  var phoneno = /^\d{9}$/;
  return phoneno.test(value);
};
export default {
  name: "money-tranfer-input",
  mixins: [validationMixin],
  props: {
    dataBackgroundColor: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      input:{ 
        accountNumber:null,
        info:null,
        money:null,
        bankName:null,
        note:null
      },
      resMess:{
        stat:"",
        mess:""
      }
    };
  },
  validations:{
    input:{
        accountNumber:{
            required,
            accountNum
        },
        info:{
            required
        },
        money:{
            required,
            valid: minValue(50000)
        },
        bankName:{
            required
        },
        note:{
            required
        }
    }
  },
  computed:{
    
  },
  methods:{
    getValidationClass (fieldName) {
        const field = this.$v.input[fieldName]

        if (field) {
          return {
            'md-invalid': field.$invalid && field.$dirty
          }
        }
      },
      handleInput () {
        this.$v.$touch()
        if (!this.$v.$invalid) {
          this.$store.dispatch('updatebalance',this.input).then(res=>{
              //thng bo
            this.$notify({ 
              message:
              this.$store.state.statusUpdateRes.mess,
              icon: "add_alert",
              horizontalAlign: "top",
              verticalAlign: "bottom",
              type: this.$store.state.statusUpdateRes.stat
            });
          });
          console.log(this.input);
          
        }
      },
  },
};
</script>
<style></style>
