<template>
  <form @submit.prevent="handleInput">
    <md-card>
        <md-card-header :data-background-color="dataBackgroundColor">
        <h4 class="title">Bng in thng tin</h4>
        </md-card-header>
      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field class="md-layout-item md-small-size-100 md-size-70" :class="getValidationClass('username')">
              <label>Tn khch hng</label>
              <md-input type="text" v-model="input.username"></md-input>
              <span class="md-error" v-if="!$v.input.username.required">Tn khch hng khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-33" >
            <md-field :class="getValidationClass('phone')">
              <label>S in thoi</label>
              <md-input type="tel" v-model="input.phone"></md-input>
              <span class="md-error" v-if="!$v.input.phone.required">S in thoi khng th b trng</span>
              <span class="md-error" v-else-if="!$v.input.phone.phone">S in thoi khng hp l</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field :class="getValidationClass('email')">
              <label>Email</label>
              <md-input type="email" v-model="input.email"></md-input>
              <span class="md-error" v-if="!$v.input.email.required">Email khng th b trng</span>
              <span class="md-error" v-else-if="!$v.input.email.email">Email khng hp l</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-60">
            <md-field :class="getValidationClass('address')">
              <label>a ch</label>
              <md-input type="text" v-model="input.address"></md-input>
              <span class="md-error" v-if="!$v.input.address.required">a ch khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-40" >
            <md-field :class="getValidationClass('indentity')">
              <label>S chng minh nhn dn</label>
              <md-input type="text" v-model="input.indentity"></md-input>
              <span class="md-error" v-if="!$v.input.indentity.required">CMND khng th b trng</span>
              <span class="md-error" v-else-if="!$v.input.indentity.indentity">CMND khng hp l</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50" >
              <md-datepicker v-model="input.birthDay" :class="getValidationClass('birthDay')">
                <label>Ngy sinh</label>
                <span class="md-error" v-if="!$v.input.birthDay.required">Ngy sinh khng th b trng</span>
                <span class="md-error" v-else-if="!$v.input.birthDay.day">Ngy sinh khng hp l</span>
              </md-datepicker>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field :class="getValidationClass('sex')">
            <label>Gii tnh</label>
            <md-select v-model="input.sex" >
                <md-option value="Nam">Nam</md-option>
                <md-option value="N">N</md-option>
                <md-option value="Khc">Khc</md-option>
            </md-select>
            <span class="md-error" v-if="!$v.input.sex.required">Gii tnh khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-40">
            <md-field>
              <label>Ngh nghip</label>
              <md-input type="text" v-model="input.job"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-40">
            <md-field>
              <label>Chc v</label>
              <md-input type="text" v-model="input.position"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field :class="getValidationClass('accountName')">
              <label>Tn ti khon</label>
              <md-input type="text" v-model="input.accountName"></md-input>
              <span class="md-error" v-if="!$v.input.accountName.required">Tn ti khon khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field :class="getValidationClass('password')">
              <label>Mt khu</label>
              <md-input type="text" v-model="input.password"></md-input>
              <span class="md-error" v-if="!$v.input.password.required">Mt khu khng th b trng</span>
            </md-field>
          </div>
           <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field :class="getValidationClass('rePassword')">
              <label>Nhp li mt khu</label>
              <md-input type="text" v-model="input.rePassword"></md-input>
              <span class="md-error" v-if="!$v.input.rePassword.required">Nhp li mt khu</span>
              <span class="md-error" v-else-if="!$v.input.rePassword.sameAsPassword">Mt khu khng gin</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field class="md-layout-item md-small-size-100 md-size-50" :class="getValidationClass('branch')">
              <label>Chi nhnh</label>
              <md-input type="text" v-model="input.branch"></md-input>
              <span class="md-error" v-if="!$v.input.branch.required">Chi nhnh khng th b trng</span>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field>
              <label>Ghi ch</label>
              <md-input type="text" v-model="input.note"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-100 text-right">
            <md-button class="md-raised md-success" type="submit" >To ti khon</md-button>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </form>
</template>
<script>
const moment=require('moment');
import { validationMixin } from 'vuelidate'
import {
  required,
  email,
  sameAs,
  minLength,
  maxLength
} from 'vuelidate/lib/validators';
const day = (value, vm) => {
  return moment(value,"YYYY-MM-DD").isValid();
};
const phone = (value, vm) => {
  var phoneno = /^\d{9}$/;
  return phoneno.test(value);
};
const indentity = (value, vm) => {
  var phoneno = /^\d{10}$/;
  return phoneno.test(value);
};
export default {
  name: "account-input",
  mixins: [validationMixin],
  props: {
    dataBackgroundColor: {
      type: String,
      default: ""
    }
  },
  data() {
    //let now= new Date();
    let date = moment().format("YYYY-MM-DD");
    return {
      input:{ 
        username:null,
        phone:null,
        indentity:null,
        email:null,
        address:null,
        birthDay:date,
        sex:null,
        accountName:null,
        password:null,
        rePassword:null,
        branch:null,
        job:null,
        position:null,
        note:null
      },
      resMess:{
        stat:"",
        mess:""
      }
    };
  },
  validations:{
    input:{
      username:{
          required
        },
        phone: {
          required,
          phone
        },
        indentity:{
          required,
          indentity
        },
        email:{
          required,
          email
        },
        address:{
          required,
        },
        birthDay:{
          required,
          day
        },
        sex:{
          required,
        },
        accountName:{
          required,
        },
        password:{
          required,
        },
        rePassword:{
          required,
          sameAsPassword: sameAs('password')
        },
        branch:{
          required,
        }
    }
  },
  computed:{
    
  },
  methods:{
    getValidationClass (fieldName) {
        const field = this.$v.input[fieldName]

        if (field) {
          return {
            'md-invalid': field.$invalid && field.$dirty
          }
        }
      },
      handleInput () {
        this.$v.$touch()
        if (!this.$v.$invalid) {
          this.$store.dispatch('createAccount',this.input).then(res=>{
            this.$notify({ 
              message:
              this.$store.state.statusRes.mess,
              icon: "add_alert",
              horizontalAlign: "top",
              verticalAlign: "bottom",
              type: this.$store.state.statusRes.stat
            });
          });
          //console.log(this.input);
          //Thong bao
        }
      },
  },
};
</script>
